@font-face {
  font-family: 'Bladerunner';
  src: url('https://fonts.cdnfonts.com/s/19375/BLADRMF_.woff') format('woff');
  font-display: swap; /* Add this line */
}

body, html, #root, .App {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Bladerunner', 'Roboto', 'Helvetica', 'Arial', sans-serif;
  background-color: #121212;
  color: #ffffff;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #1e1e1e;
}

::-webkit-scrollbar-thumb {
  background: #ff4081;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #00e5ff;
}

.gm-style {
  font-family: 'Bladerunner', 'Roboto', 'Helvetica', 'Arial', sans-serif !important;
}

.gm-style-iw {
  background-color: #1e1e1e !important;
  color: #ffffff !important;
}

.gm-style-iw button {
  filter: invert(100%) !important;
}

.gm-style-iw * {
  color: #ffffff !important;
}
.highlighted-location {
  background-color: #FFEB3B;
  padding: 2px 4px;
  border-radius: 3px;
  cursor: pointer;
  font-weight: bold;
  color: #121212; /* This ensures the text is visible on the yellow background */
}